/* eslint-disable import/no-duplicates */
import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import {
  FormControl,
  Select,
  MenuItem,
  InputBase,
  Card,
} from '@material-ui/core';
import { createMuiTheme } from '@material-ui/core/styles';
import Table from '../teams/Table';
import styles from '../../styles/TeamView.css';
import { UserContext } from '../../contexts/UserContext';

const BootstrapInput = withStyles(theme => ({
  root: {
    width: '300px',
    height: '42px',
    marginTop: theme.spacing(3),
    overflowX: 'auto',
    margin: '20px auto',
    display: 'flex'
  },
  input: {
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    width: '400px',
    padding: '10px 26px 10px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    table: {
      minWidth: 700
    }
  }
}))(InputBase);

class TeamView extends Component {
  static contextType = UserContext;

  constructor(props) {
    super(props);
    this.theme = createMuiTheme({
      overrides: {
        MuiToolbar: {
          root: {
            backgroundColor: 'white'
          }
        }
      }
    });
  }

  state = {
    team: null,
  };

  componentDidMount() {
    const { teams: allTeams } = this.context;
    this.setState({
      team: allTeams.filter(t => t.team.name !== 'Personal')[0]
    });
  }

  selectTeam = event => {
    this.setState({ team: event.target.value });
  };

  render() {
    const { classes } = this.props;
    const { team } = this.state;
    const { user, teams: allTeams } = this.context;

    const teams = allTeams.filter(t => t.team.name !== 'Personal');

    return (
      <div className={classes.bodyContainer} style={{ display: 'flex' }}>
        <Card style={{ margin: 'auto' }}>
          <div
            style={{
              width: 'fit-content',
              margin: 'auto',
              paddingTop: '30px',
              fontSize: '35px',
              userSelect: 'none'
            }}
          >
            Teams
            <span
              style={{
                width: '73px',
                height: '4px',
                margin: '8px auto 0',
                display: 'block',
                backgroundColor: '#f5a623'
              }}
            />
          </div>
          <div style={{ padding: '30px', margin: 'auto' }}>
            <div style={{ margin: 'auto', width: 'fit-content' }}>
              <FormControl className={classes.margin}>
                <Select
                  className={classes.selectEmpty}
                  value={team || {}}
                  onChange={e => this.selectTeam(e)}
                  input={
                    <BootstrapInput
                      name="nameSelected"
                      id="age-customized-select"
                    />
                  }
                >
                  {teams.map(t => {
                    return (
                      <MenuItem key={t.team.id} value={t}>
                        {t.team.name}
                      </MenuItem>
                    );
                  })}
                </Select>
              </FormControl>
            </div>

            {team && <Table team={team} user={user} />}
          </div>
        </Card>
      </div>
    );
  }
}

export default withStyles(styles)(TeamView);
