/* eslint-disable camelcase */
import React, { Component, forwardRef } from 'react';
import MaterialTable from 'material-table';
import { Notification } from 'element-react';
import { Checkbox } from '@material-ui/core';
import { withStyles , createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import AddBox from '@material-ui/icons/AddBox';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import styles from '../../styles/TeamView.css';
import TeamAPI from '../../api/TeamAPI';

const tableIcons = {
  Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
  Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
  DetailPanel: forwardRef((props, ref) => (
    <ChevronRight {...props} ref={ref} />
  )),
  Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
  Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => (
    <ChevronLeft {...props} ref={ref} />
  )),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
  ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
  ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
};

class Table extends Component {
  state = {
    data: [],
    ownOrgsIds: []
  };

  constructor(props) {
    super(props);
    this.theme = createMuiTheme({
      overrides: {
        MuiTablePagination: {
          select: {
            marginRight: '20px'
          }
        },
        MuiToolbar: {
          root: {
            backgroundColor: 'white'
          }
        },
        MuiTableCell: {
          paddingCheckbox: {
            width: 'auto',
            padding: 'auto'
          }
        },
        MuiButtonBase: {
          root: {
            fontSize: '16px'
          }
        },
        MuiTableSortLabel: {
          active: {
            color: 'white'
          }
        }
      },
      palette: {
        primary: {
          main: '#4caf50'
        },
        secondary: {
          main: '#ff9100'
        }
      }
    });
  }

  componentDidMount() {
    TeamAPI.getMembers(this.props.team.team.id).then(members => {
      this.setState({
        data: members.map(member => {
          return {
            username: member.username,
            isTeamLeader:
              member.teams.filter(
                team => team.team === this.props.team.team.id
              )[0].level === 'LEADER'
          };
        })
      });
    });
    this.setState({
      ownOrgsIds: this.props.user.organizations.filter(o => o.isOwned === true).map(o => o.id)
    })
  }

  handleUpdateRole = (e, username) => {
    if (username === this.props.user.username) {
      Notification.error({
        title: 'Error',
        message: "You can't change your own rights"
      });
    } else {
      const checked = e.target.checked;
      TeamAPI.updateMember(
        this.props.team.team.id,
        username,
        checked ? 'LEADER' : 'MEMBER'
      ).then(() => {
        this.setState({
          data: this.state.data.map(member => {
            if (member.username === username) {
              return {
                username,
                isTeamLeader: checked
              };
            }
            return member;
          })
        });
        Notification.success({
          title: 'Success',
          message: 'Rights have been changed'
        });
      }).catch(() => {
        Notification.error({
          title: 'Error',
          message: 'An error occurred while changing the rights'
        });
      });
    }
  };

  UNSAFE_componentWillReceiveProps(props) {
    TeamAPI.getMembers(props.team.team.id).then(members => {
      this.setState({
        data: members.map(member => {
          return {
            username: member.username,
            isTeamLeader:
              member.teams.filter(team => team.team === props.team.team.id)[0].level === 'LEADER'
          };
        })
      });
    });
  }

  render() {
    return (
      <ThemeProvider theme={this.theme}>
        <MaterialTable
          title="Team members"
          options={{
            headerStyle: {
              backgroundColor: '#324157',
              color: '#FFF'
            }
          }}
          icons={tableIcons}
          columns={[
            {
              title: 'User e-mail',
              field: 'username',
              render: rowData => <p style={{ width: '16rem' }}>{rowData.username}</p>
            },
            {
              title: 'Team Leader',
              field: 'isTeamLeader',
              editable: 'onUpdate',
              render: rowData => {
                return (
                  rowData && (
                    <Checkbox
                      checked={rowData.isTeamLeader}
                      onChange={e => this.handleUpdateRole(e, rowData.username)}
                      disabled={
                        this.props.team.level === 'MEMBER'
                          && !this.state.ownOrgsIds.includes(this.props.team.team.organization)
                          || rowData.username === this.props.user.username
                      }
                      style={{ float: "right" }}
                    />
                  )
                );
              }
            }
          ]}
          data={this.state.data}
          editable={{
            onRowAdd: newData =>
              new Promise((resolve) => {
                setTimeout(() => {
                  TeamAPI.addMember(
                    this.props.team.team.id,
                    newData.username,
                    newData.isTeamLeader ? 'LEADER' : 'MEMBER'
                  ).then(() => {
                      const data = this.state.data;
                      data.push(newData);
                      this.setState({ data }, () => resolve());
                      Notification.success({
                        title: 'Success',
                        message: 'User successfully added to the team'
                      });
                    }).catch(err => {
                      if (err.response.status === 403) {
                        Notification.error({
                          title: 'Error',
                          message: 'Only team leaders can add a member to your team.'
                        });
                      } else {
                        Notification.error({
                          title: 'Error',
                          message: err.response.data.errorMessage || 'An error occurred while adding the user to the team'
                        });
                      }
                    });
                  resolve();
                }, 1000);
              }),
            onRowDelete: oldData =>
              new Promise((resolve) => {
                setTimeout(() => {
                  TeamAPI.deleteMember(
                    this.props.team.team.id,
                    oldData.username
                  )
                    .then(() => {
                      const data = this.state.data;
                      const index = data.indexOf(oldData);
                      data.splice(index, 1);
                      this.setState({ data }, () => resolve());
                      Notification.success({
                        title: 'Success',
                        message: 'User successfully removed from the team'
                      });
                    })
                    .catch(() => {
                      Notification.error({
                        title: 'Error',
                        message:
                          'An error occurred while deleting the user from the team'
                      });
                    });
                  resolve();
                }, 1000);
              })
          }}
        />
      </ThemeProvider>
    );
  }
}

export default withStyles(styles)(Table);
